<template>
    <div class="row text-center py-1">
        <div class="col" v-if="set.mobile">
            <div class="border rounded" @click="$tourl('user_mobile')"><span class="bi bi-tablet-fill me-1"></span>改手机号</div>
        </div>
        <div class="col" v-if="set.cancel">
            <div class="border rounded"  @click="$emit('onsubid',set.cancel)"><span class="bi bi-bookmark-x-fill me-1"></span>取消订单</div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
    show:{},
  },
}
</script>

