<template>
    <div class="d-flex justify-content-between align-items-center py-2">
        <div class="d-flex flex-row bd-highlight">
            <div class="bd-highlight">
                <img :src="$store.state.imgurl+'/assets/img/userimg/'+show.avatar+'.jpg'" width="50" height="50" class="rounded-pill border border-2 border-white">
            </div>
            <div class="bd-highlight ms-2">
                <div class="fw-bold fs-6">{{show.nickname}}</div>
                <div>{{show.pevaluate}} 分 - 出行<span class="text-primary">{{show.pint}}</span>次</div>
            </div>
        </div>
        <div class="bd-highlight">
            <span class="d-inline-block text-center border rounded-pill bg-white fs-6  mx-1 bi bi-telephone-fill" :class="telephone.state.includes(show.state)?'text-dark ':'text-black-50'" @click="$emit('ontoast',chat.state.includes(show.state),telephone)" style="width:30px; height:30px;"></span>
            <span class="d-inline-block text-center border rounded-pill bg-white fs-6 mx-1 bi bi-chat-dots-fill" :class="chat.state.includes(show.state)?'text-dark ':'text-black-50'" @click="$emit('ontoast',chat.state.includes(show.state),chat)"   style="width:30px; height:30px;"></span>
        </div>
    </div>
</template>

<script>
import Iconspill from "../../block/icons/pill-icons";
export default {
  components: {
    Iconspill,
  },
  props: {
    set:{},
    data:{},
    show:{},
  },
  data(){
    return { 
      "telephone": { "state": ['2','22','31','32','33','41','42','43','51','52'], "toast": { "text": "请接单后,才可电话联系" } , "href": ["tel:", "mobile"]},
      "chat": { "state":['2','22','31','32','33','41','42','43','51','52'],"toast": { "text": "请接单后,才可以发消息" } , "url": ["d_tchat-", "id"] } }
  }
}
</script>



